import { Divider, Form, Input, Button, Upload } from "antd";
import "./index.css";
import { CheckOutlined } from "@ant-design/icons";

function UploadPage() {
  const onSubmit = (values) => {
    console.log(values);
  };

  return (
    <div id="upload-container">
      <Form name=" " onFinish={onSubmit}>
        <Form.Item
          name="upload"
          label={<div className="upload-label"> </div>}
        >
          <Upload>
            <div id="upload-img-placeholder">
              <img src="/images/icons/camera.png"></img>
              <span>  .</span>
            </div>
          </Upload>
        </Form.Item>
        <Divider />
        <Form.Item
          name="title"
          label={<div className="upload-label"></div>}
          rules={[{ required: true, message: "  ." }]}
        >
          <Input
            className="upload-place"
            size="large"
            placeholder=" ."
          ></Input>
        </Form.Item>
        <Divider />
        <Form.Item
          name="date"
          label={<div className="upload-label"> </div>}
          rules={[{ required: true, message: " ." }]}
        >
          <Input
            className="upload-place"
            size="large"
            placeholder="  ."
          ></Input>
        </Form.Item>
        <Divider />
        <Form.Item
          name="description"
          label={<div className="upload-label"> </div>}
          rules={[{ required: true, message: "  ." }]}
        >
          <Input.TextArea
            id="product-description"
            size="large"
            placeholder=" ."
            showCount
            maxLength={300}
          ></Input.TextArea>
        </Form.Item>
        <Divider />
        <Form.Item
          label={<div className="upload-label"></div>}
          name="place"
          rules={[{ required: true, message: " " }]}
        >
          <Input
            className="upload-place"
            size="large"
            placeholder=" ."
          ></Input>
        </Form.Item>
        <Form.Item>
          <Button
            icon={<CheckOutlined />}
            id="submit-button"
            size="large"
            htmlType="submit"
          >
             
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
}

export default UploadPage;
